<template>
    <!-- 自定义指令theme,wide传的参数(里面若是字符串 要单引号引起来) 到main.js页面里 -->
    <!-- 自定义指令后面也可以像v-on:click这样绑定点击事件去做绑定一个自定义事件column, 到main.js里去弄判断有无column这个参数... -->
  <div v-theme:column="'narrow'" id="show-blogs">
      <h1>博客总览</h1>
      <!-- 搜索框 -->
      <input type="text" v-model="search" placeholder="搜索">

      <!-- 博客条目内容展示陈列 -->
      <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
          <!-- v-rainbow自定义的指令 可以给值也可以不给值 -->
          <!-- 使用filter创建过滤器 要在你要使用的地方后面加管道及名字: | 过虑器名字自己定义,然后到main.js页去实现下过滤器 -->
          <!-- 给标题添加路由链接 并将跳转到博客个人页面展示的路由后面 绑定拼接路由参数传参（下面请求获取到的id...） -->
          <!-- routes.js页面路由那个人展示路由绑定了路由参数id  所以这里也需要绑定拼接上路由参数id -->
          <router-link :to="'/blog/' + blog.id">
              <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
          </router-link>
            <article>
                <!-- 自定义过滤器：snippet  去main.js页实现-->
                {{blog.content | snippet}}    
            </article> 
      </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
      return {
          blogs:[],
          search:""
      }
  },

  created(){
    //   网络请求或本地请求json文件 
    //   this.$http.get("https://jsonplaceholder.typicode.com/posts")
      this.$http.get("https://wd0178664159bdjgtj.wilddogio.com/posts.json")
            .then(function(data){
                return data.json();//将解析的数据->对象返回去
                // console.log(data.json());//数据json解析下当做一个对象打印               
                // this.blogs = data.body.slice(0,10);//请求到值赋值给上面的属性blogs
                // console.log(this.blogs)
            })
            .then(function(data){
                var blogsArray = [];
                // 遍历返回来的数据对象
                for(let key in data){
                    // console.log(data[key]);//打印出对象
                    data[key].id = key; //将key赋值给对象作为id用
                    blogsArray.push(data[key]);//将对象放到空数组里
                    this.blogs = blogsArray;//将这个数组赋值给上面的属性blogs数组
                    // console.log(this.blogs);
                }
            })
            .catch((err) => {
                if(err) throw err;
            });
  },

    computed:{
        // 实现搜索 拿双向绑定输入框获取到的内容与blogs请求到的内容进行匹配 
        filteredBlogs:function(){
            // 把搜索到的相关内容对象返回给filteredBlogs函数 上面就遍历这个属性函数
            return this.blogs.filter((blog) => {
                return blog.title.match(this.search);//用数据库获取到的标题与搜索框的输入内容匹配
            })
        }
    },

    // 局部过滤实现 不用全局的
    filters:{
        // "to-uppercase":function(value){
        //     return value.toUpperCase(); //转换成大写
        // }

        // 或者上面的不用  用es6的写法
        toUppercase(value){
            return value.toUpperCase();//转换成大写
        }

    },

    // 局部的 
    directives:{
        'rainbow':{
            bind(el,binding,vnode){
                el.style.color = "#" + Math.random().toString(16).slice(2,8);//自由变换的6位色彩数颜色

            }
        }
    }
    

}
</script>

<style scoped>
#show-blogs{
    max-width:800px;
    margin: 0 auto;
}

.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eeeeee;
}

#show-blogs a{
    color: #444;
    text-decoration: none;
}

input[type="text"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}
</style>



