<template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form v-if="!submmited">
          <label>博客标题</label>
          <input type="text" v-model="blog.title" required>
          <label>博客内容</label>
          <textarea v-model="blog.content"></textarea>

          <div id="checkboxes">
              <label>Vue.js</label>
              <input type="checkbox" value="Vue.js" v-model="blog.categories">
              <label>Node.js</label>
              <input type="checkbox" value="Node.js" v-model="blog.categories">
              <label>React.js</label>
              <input type="checkbox" value="React.js" v-model="blog.categories">
              <label>Angular.js</label>
              <input type="checkbox" value="Angular.js" v-model="blog.categories">
          </div>  
          <label>作者</label>
          <select v-model="blog.author">
              <option v-for="author in authors" :key="author.id">
                  {{author}}
              </option>
          </select>
          <!-- 添加个属性prevent可以阻止点击时跳转刷新页面 -->
          <button @click.prevent="post">添加博客</button>

      </form>
      <div v-if="submmited">
          <h3>您的博客发送成功！</h3>
      </div>
      <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题：{{blog.title}}</p>
          <p>博客内容：</p>
          <p>{{blog.content}}</p>
          <p>博客分类：</p>
          <ul>
              <li v-for="categorie in blog.categories" :key="categorie.id">{{categorie}}</li>
          </ul>
      </div>
      <p>作者：{{blog.author}}</p>
  </div>
</template>

<script>

// https://jsonplaceholder.typicode.com/
// https://jsonplaceholder.typicode.com/posts
export default {
  name: 'add-blog',
  data () {
    return {
        blog:{
            title:"",
            content:"",
            categories:[],
            author:""
        },
        authors:["时间","吧啦吧啦","Bucky"],
        submmited:false
    }
  },

  methods:{
      post:function(){
        //   点击按钮传送信息
          this.$http.post("https://wd0178664159bdjgtj.wilddogio.com/posts.json",this.blog
            // {
            //     title:this.blog.title,
            //     body:this.blog.content,
            //     userId:1
            // }
          )
                .then(function(data){
                    // console.log(data);
                    this.submmited = true;
                    this.$router.push('/');//成功后跳转到根页
                })
      }
  }
}
</script>


<style scoped>
/* #add-blog * 意思是让#add-blog下面所有的内容都以border-box展示 */
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label{
    display: block;
    margin: 20px 0 10px;
}

input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
}

textarea{
    height: 200px;
}

#checkboxes label{
    display: inline-block;
    margin-top:0;
}

#checkboxes input{
    display: inline-block;
    margin-right:10px;
}

button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #ffffff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}

#preview{
    padding: 10px 20px;
    border: 1px dotted #cccccc;
    margin: 30px 0;
}

h3{
    margin-top: 10px;
}
</style>



