// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import App from './App'
import Routes from './routes'

Vue.config.productionTip = false

// 使用下VueResource
Vue.use(VueResource);

Vue.use(VueRouter);


// 全局的
// 自定义指令 第一个参数是自定义指令的名字 第二个参数是个对象 里面可以给你这个指令绑定你要执行的东西
// Vue.directive('rainbow',{
//     // 钩子函数 bind只调用一次 可执行一次初始化动作
//     // el代表使用这令的这个元素
//     bind(el,binding,vnode){
//       el.style.color = "#" + Math.random().toString(16).slice(2,8);//自由变换的6位色彩数颜色

//     }
// });

Vue.directive('theme',{
  // 传过来的参数相当于binding对象里面的属性东西等
    bind(el,binding,vnode){
      // 如果bingding.value获取到的参数值等于wide时  则执行下面宽度
        if(binding.value == 'wide'){
          el.style.maxWidth = '1260px';
        }else if(binding.value == "narrow"){//如果获取到传过来的参数是narrow 则执行560px宽
          el.style.maxWidth = "560px";
        }

        // 判断ShowBlogs.vue页传来的自定义指令 的参数是啥 然后执行...
        if(binding.arg == 'column'){
            el.style.background = "#6677cc";
            el.style.padding = '20px';
        }

    }
})


// 全局的
// filter创建自定义的过滤器 实现功能  ShowBlogs.vue页要处理的值通过管道传给过滤器带过来的 value形参
// Vue.filter("to-uppercase",function(value){
//     return value.toUpperCase(); //value接收到要处理的值 转换成大写
// })

// 将展示的内容简短显示
Vue.filter("snippet",function(value){
    return value.slice(0,100) + "...";
})



// 创建路由 实例化
const router = new VueRouter({
    routes:Routes,//使用路由
    mode:"history",//不要#号使用这个  
})



/* eslint-disable no-new */
new Vue({
  router:router,//在哪个页面或容器使用路由
  el: '#app',
  components: { App },
  template: '<App/>'
})



