<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类：</p>
        <ul>
            <li v-for="category in blog.categories" :key="category.id">
                {{category}}
            </li>
        </ul>
    </div>
</template>


<script>
export default {
    name:"single-blog",
    data(){
        return {
            id:this.$route.params.id,//拿到routes.js页路由后绑定的参数id
            blog:{}
        }
    },

    created(){
        // 请求获取路由参数id的那条内容  后面拼接id  .json
        this.$http.get('https://wd0178664159bdjgtj.wilddogio.com/posts/' + this.id + ".json")
            .then(function(data){
                // console.log(data);
                // this.blog = data.body;//将请求到的内容赋值给blog
                return data.json();//解析json数据成对象 并返回去
            })
            .then(function(data){
                this.blog = data;//将上面返回去的值获得到并赋值给blog
            })
    }
}
</script>

<style scoped>
    #single-blog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eeeeee;
        border: 1px dotted #aaa;
    }
</style>





